:root {
  --color-glass: rgba(255, 255, 255, 0.05);
  --color-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
}
.text {
  margin: 0 20px 0 20px;
  font-family: monospace;
  padding-bottom: 20px;
}
.about {
  height: 93%;
  width: 100%;
  min-width: 700px;
  min-height: 626px;
  color: rgb(255, 255, 255);
  font-family: monospace;
  font-size: 30px;
  text-align: center;
  position: absolute;
  background-image: url("../img/snowman-gd42f9d18a_1280.jpg");
  background-repeat: round;
}
.comment {
  height: 93%;
  width: 100%;
  min-width: 700px;
  min-height: 626px;
  color: aliceblue;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  background-image: url("../img/stars-g0a3a36647_1280.jpg");
  background-repeat: round;
  background-size: cover;
}

input {
  width: 250px;
  height: 25px;
  background-color: var(--color-glass);
  backdrop-filter: blur(12px);
  box-shadow: var(--color-shadow);
  border-radius: 8px;
  color: white;
  border: 1px solid hwb(180 1% 0%);
}
button {
  width: 255px;
  height: 30px;
  color: white;
  font-weight: 700;
  font-size: large;
  text-align: center;
  background-color: var(--color-glass);
  backdrop-filter: blur(12px);
  box-shadow: var(--color-shadow);
  border-radius: 8px;
  border: 1px solid hwb(180 1% 0%);
}
.greetingsBox {
  font-family: "Times New Roman", Times, serif;
  width: 500px;
  height: 150px;
  border: 3px solid hwb(180 1% 0%);
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  background-color: var(--color-glass);
  backdrop-filter: blur(12px);
  box-shadow: var(--color-shadow);
  letter-spacing: 0.1rem;
  font-size: 20px;
  border-radius: 8px;
}
